.container__table {
    font-size: 8px;
    border-collapse: model;
    border-collapse: separate;
    border: 3px solid black;
}
.container__table-h3 {
    font-size: 12px;
    text-align: center;
    margin-bottom: 10px;
}
th {
    padding: 5px;
}

@media(min-width:400px){
    .container__table {
        font-size: 12px;
    }
    th {
        padding: 10px;
    }
}

@media(min-width:650px){
    .container__table {
        font-size: 16px;
    }
    th {
        padding: 10px;
    }
}

@media(min-width:1000px){
    .container__table {
        font-size: 18px;
    
    }
     th {
        padding: 30px;
    }
}